import Vue from 'vue'
import VueRouter from 'vue-router'
const Login = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Login')
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Home')
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome')

const Users = () => import(/* webpackChunkName: "user_rights_roles" */ '../components/users/Users')
const Rights = () => import(/* webpackChunkName: "user_rights_roles" */ '../components/power/Rights')
const Roles = () => import(/* webpackChunkName: "user_rights_roles" */ '../components/power/Roles')

const Categories = () => import(/* webpackChunkName: "categories_params" */ '../components/goods/Categories')
const Params = () => import(/* webpackChunkName: "categories_params" */ '../components/goods/Params')

const List = () => import(/* webpackChunkName: "list_add" */ '../components/goods/List')
const Add = () => import(/* webpackChunkName: "list_add" */ '../components/goods/Add')

const Order = () => import(/* webpackChunkName: "order_report" */ '../components/order/Order')
const Report = () => import(/* webpackChunkName: "order_report" */ '../components/report/Report')

//用户来源echarts图表
const UserResourceLine = () => import(/* webpackChunkName: "echarts" */ '../components/echarts/UserResourceLine')
Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    //访问网页，重定向为登录页面
    { path: '/', redirect: '/login' },
    //访问数据报表重定向为 用户echarts图表
    {path:'/reports', redirect: '/userresourceline'},
    { path: '/login', component: Login },
    {
      path: '/home',
      component: Home,
      //独享路由守卫,判断是否处于登录状态
      beforeEnter(to,from,next){
        if(to.path === '/login') return next()
        const tokenStr = window.sessionStorage.getItem('token')
        if(!tokenStr) next('/login')
        next()
      },
      children:[
        { path: '/welcome', component: Welcome },
        { path: '/users', component: Users },
        { path: '/rights', component: Rights },
        { path: '/roles', component: Roles },
        { path: '/categories', component: Categories },
        { path: '/params' , component: Params },
        { path: '/goods', component: List },
        { path: '/goods/addpage', component: Add},
        { path: '/orders', component: Order},
        { path: '/reports', component: Report,
          children:[{path: '/userresourceline',component: UserResourceLine}]
        }
      ],
      //进入 home 重定向为 welcome
      redirect: '/welcome'
    }
  ]
})

export default router
